.App {
  font-family: "Barlow", sans-serif;
  text-align: center;
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow: auto;
}

h1 {
  font-family: "DM Serif Text", serif;
}

.particlesCanvas {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fcfcfc;
  z-index: 0;
  margin: 0;
  padding: 0;
  border: 0;
}

#mainContainer {
  color: #000000;
  position: relative;
  width: fit-content;
  min-width: 50vw;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vh;
  margin-bottom: 10vh;
  min-height: 40vh;
  border-radius: 10px;
  z-index: 2;
  box-shadow: 1px 1px 14px -3px rgba(0, 0, 0, 0.4);
  padding: 2rem;
}

.navLink {
  color: #000000;
  margin-right: 1rem;
  font-weight: 900;
}

.active-link {
  color: #072ac8;
  border-bottom: 3px solid white;
}

.highlight {
  color: #072ac8;
}

.highlight2 {
  color: #758ecd;
}
